.myplace__modal {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.8);
  z-index: 2;

  &.hidden {
    display: none;
  }

  .modal__modal {
    position: relative;
    width: 300px;
    height: 200px;
    background-color: #f8f8f8;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .modal__close__btn{
      position: absolute;
      right: -5%;
      top: -5%;
      img{
        height: 32px;
        width: 32px;
      }
    }

    span {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      margin: 23px auto 0;

      .modal__picture {
        margin: 0 auto 0;

        width: 29px;
        height: 29px;
      }
    }

    .modal__title {
      margin: 14px auto 0;
      width: (335/600)*100%;
      height: (26/406)*100%;
      font-family: Nanum Gothic,sans-serif;
      font-size: 13px;
      font-weight: bold;
      line-height: 1.48;
      text-align: center;
      color: #3e3e3e;
    }

    .modal__content {
      width: 100%;
      height: 100%;
      justify-content: center;
      display: flex;
      flex-direction: column;

      .modal__contenttext {
        margin: 7px auto 40px;
        width: 90%;
        height: 100%;
        border-radius: 7px;
        border: solid 2px #454545;
        color: black;
      }

      .modal__contentsubmit {
        margin: auto auto 6px;
        width: 33.33%;
        height: 100%;
        font-size: 11px;
        line-height: 0.83;
        letter-spacing: 0.58px;
        color: #ffffff;
        border-radius: 10px;
        background-color: #333333;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
}
.myplaceDetailList {
  margin-top: 63px;
  width: 100%;
}

.myplace__favorite{
  padding-top: 10px;
}

.myplace__search__container{
  width: 90%;
  margin: 0 auto;

  border-bottom: 2px #b4022f solid;
  .search__input{
    border: 2px #d6d6d6 solid;
    border-radius: 10px;
    margin: 17px 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    #content{
      width: 85%;
      font-size: large;
      border: none;
    }

    img{
      display: inline-block;
      width: 23px;
      height: 23px;
      padding: 10px;
    }

    #search{
      width: 24px;
      height: 24px;
      background: #b4022f;
      padding: 10px;
      border-radius: 10px;
      /*margin-left: 10px;*/
    }
  }
}